<template>
    <div class="shows" v-if="initialized">
        <p class="md-headline title">Shows</p>
        <div class="show-grid">
            <div class="shows">
                <plex-card
                        v-for="show in shows" :key="show.title"
                        class="plex-card"
                        :title="show.title"
                        :to="`/show/?id=${show.key}`"
                        :image="show.info.thumb"/>
            </div>
        </div>
    </div>
</template>

<script>
    import ServerApi from "@/js/ServerApi";
    import PlexCard from "../components/PlexCard";

    console.log(ServerApi);

    export default {
        props: {
            initialized: Boolean,
        },
        name: 'shows',
        components: {PlexCard},
        data() {
            return {
                shows: []
            }
        },
        async mounted() {
            if (this.initialized) {
                this.initialize();
            } else {
                ServerApi.addEventListener('authenticated', this.initialize);
            }
        },
        methods: {
            async initialize() {
                this.shows = await ServerApi.shows();
            }
        },
        beforeDestroy() {
            ServerApi.removeEventListener('authenticated', this.initialize)
        },
        watch: {
            $route() {
                this.initialize();
            }
        }
    }
</script>

<style scoped>
    .shows {
        overflow: hidden !important;
    }

    .title {
        text-align: center;
    }

    .show-grid {
        position: relative;
        left: 5px;
        max-width: 1000px;
        display: block;
        margin: 0 auto;
    }

    .plex-card {
        display: inline-block;
        width: calc(100% / 3 - 10px);
        margin-right: 10px;
        margin-bottom: 20px;
    }

    @media (min-width: 700px) {
        .plex-card {
            width: calc(100% / 4 - 10px);
        }
    }

    @media (min-width: 900px) {
        .plex-card {
            width: calc(100% / 5 - 10px);
        }
    }


</style>