<template>
    <div id="app">
        <router-view :initialized="initialized"/>
    </div>
</template>

<script>
    import ServerApi from "@/js/ServerApi";

    ServerApi.setEndpoint('https://api.ruurd.dev');


    export default {
        name: 'app',
        data() {
            return {
                initialized: false,
                routeHistory: [],
            }
        },
        async mounted() {
            if (localStorage.getItem('auth') === null && !this.$route.path.includes('/login')) {
                await this.$router.push('/login');
            } else {
                let {user, password} = JSON.parse(localStorage.auth);
                await ServerApi.auth(user, password);
                this.initialized = true;
            }
        },
        watch: {
            $route() {
                console.log(this.routeHistory);
                if (this.routeHistory.length > 0) {
                    let lastRoute = this.routeHistory[this.routeHistory.length - 1];
                    if (lastRoute.includes('login'))
                        this.$router.go();
                }
                this.routeHistory.push(this.$route.path);
            }
        }
    }
</script>

<style>
    @import url('https://fonts.googleapis.com/css?family=Material+Icons|Montserrat|Roboto:400,400i,500,700&display=swap');

    #app {
        font-family: 'Montserrat', 'Roboto', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
</style>
